import * as Yup from 'yup'

const FORM_LOGIN = {
    USERNAME: 'username',
    PASSWORD: 'password'
}

const initialValueLogin = {
    [FORM_LOGIN.USERNAME]:'',
    [FORM_LOGIN.PASSWORD]: ''
}

const validateLogin = ({usernameRequired, passwordRequired}) => Yup.object({
    [FORM_LOGIN.USERNAME]: Yup.string().required(usernameRequired),
    [FORM_LOGIN.PASSWORD]: Yup.string().required(passwordRequired)
}) 

export {FORM_LOGIN, initialValueLogin, validateLogin}