import { useState, Fragment } from 'react'
import classnames from 'classnames'
import Avatar from '@components/avatar'
import { useSkin } from '@hooks/useSkin'
import loginService from '../../../services/login'
import { useDispatch } from 'react-redux'
import { Controller, useForm } from 'react-hook-form'
import { toast, Slide } from 'react-toastify'
import { handleLogin } from '@store/actions/auth'
import { useHistory } from 'react-router-dom'
import InputPasswordToggle from '@components/input-password-toggle'
import { getHomeRouteForLoggedInUser, isObjEmpty } from '@utils'
import { Coffee } from 'react-feather'
import { Link } from 'react-router-dom'
import { injectIntl } from 'react-intl'
import { notification } from 'antd'

import {
  Row,
  Col,
  CardTitle,
  Form,
  Input,
  Label,
  CustomInput,
  Button,
} from 'reactstrap'

import { ReactComponent as Person } from '@src/assets/images/icons/person.svg'
import { ReactComponent as Key } from '@src/assets/images/icons/key.svg'
import '@styles/base/pages/login.scss'
import 'antd/dist/antd.css'
import { yupResolver } from '@hookform/resolvers/yup'
import { FORM_LOGIN, initialValueLogin, validateLogin } from '../../../@core/components/hook-form/validation/login'

const { USERNAME, PASSWORD } = FORM_LOGIN

const ToastContent = ({ name }) => (
  <Fragment>
    <div className='toastify-header'>
      <div className='title-wrapper'>
        <Avatar size='sm' color='success' icon={<Coffee size={12} />} />
        <h6 className='toast-title font-weight-bold'>Welcome, {name}</h6>
      </div>
    </div>
    <div className='toastify-body'>
      <span>. Now you can start to explore. Enjoy!</span>
    </div>
  </Fragment>
)

const Login = ({ intl }) => {
  const [skin,] = useSkin()

  const dispatch = useDispatch()

  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const history = useHistory()
  const { control, formState: { errors }, handleSubmit } = useForm({
    defaultValues: initialValueLogin,
    resolver: yupResolver(validateLogin({
      usernameRequired: '',
      passwordRequired: ''
    }))
  })

  const illustration = skin === 'dark' ? 'frame.png' : 'frame.png',
    source = require(`@src/assets/images/pages/${illustration}`).default

  const logoIllustration = skin === 'dark' ? 'logo.png' : 'logo.png',
    logoSource = require(`@src/assets/images/pages/${logoIllustration}`).default

  const liIllustration = skin === 'dark' ? 'frame_1154.png' : 'frame_1154.png',
    liIconSource = require(`@src/assets/images/pages/${liIllustration}`).default

  const fbIllustration = skin === 'dark' ? 'frame_1155.png' : 'frame_1155.png',
    fbIconSource = require(`@src/assets/images/pages/${fbIllustration}`).default

  const ggIllustration = skin === 'dark' ? 'frame_1156.png' : 'frame_1156.png',
    ggIconSource = require(`@src/assets/images/pages/${ggIllustration}`).default

  const onSubmit = data => {
    if (isObjEmpty(errors)) {
      loginService.login(data).then(result => {
        if (result && Object.keys(result).length > 0) {
          const newData = { ...result, accessToken: result.token, refreshToken: result.token }
          dispatch(handleLogin({ ...newData }))
          history.push("/")

          toast.success(
            <ToastContent name={newData.fullName || newData.username || 'John Doe'} roleName={newData.roleName || ''} />,
            { transition: Slide, hideProgressBar: true, autoClose: 2000 }
          )
        } else {
          openErrorNotification('topRight', "Your user or password is'bonnt corret !")
        }
      })
    }
  }

  const openSocialPage = (event, url) => {
    event.preventDefault()
    window.location.href = url
  }

  const openErrorNotification = (placement, message) => {
    notification.error({
      message: `${message}`,
      placement,
    });
  };

  return (
    <div className='auth-wrapper auth-v2'>
      <Row className='auth-inner-row'>
        <Col className='d-none d-lg-flex col-lg-6 col-6 login-col'>
          <div className='hobe-icons align-items-center'>
            <img className='logo-fluid' src={logoSource} alt='Login V2' />
            <Row className='btns-group'>
              <div onClick={(event) => openSocialPage(event, 'https://www.linkedin.com')}>
                <img className='li-icon-fluid' src={liIconSource} alt='Login V2' />
              </div>
              <div onClick={(event) => openSocialPage(event, 'https://www.facebook.com')}>
                <img className='fb-icon-fluid' src={fbIconSource} alt='Login V2' />
              </div>
              <div onClick={(event) => openSocialPage(event, 'https://www.google.com')}>
                <img className='gg-icon-fluid' src={ggIconSource} alt='Login V2' />
              </div>
            </Row>
          </div>
          <div className='w-100 d-lg-flex align-items-center img-wrapper'>
            <img className='img-fluid' src={source} alt='Login V2' />
          </div>
        </Col>

        <Col className='d-flex align-items-center auth-bg px-2 p-lg-5 p-7' sm='12' lg='6'>
          <div className='d-lg-none hobe-icons align-items-center'>
            <img className='logo-fluid' src={logoSource} alt='Login V2' />
          </div>
          <div className='d-flex flex-column px-xl-3 login-form' lg='4' sm='12'>
            <Form className='auth-login-form' onSubmit={handleSubmit(onSubmit)}>
              <CardTitle tag='h2' className='font-weight-bold mb-1 login-title'>
                {intl.formatMessage({ id: 'Login' })}
              </CardTitle>
              <Row className='align-items-end mx-0'>
                <Col className='col-1 p-0 col-icon d-flex align-items-center justify-content-center'>
                  <Person className='person-icon' />
                </Col>
                <Col className='col-11 p-0'>
                  <Controller name={USERNAME} control={control} render={({ field: { value, onChange } }) =>
                    <div className={classnames('user-input d-flex flex-column h-100', { 'is-invalid': errors[USERNAME] })}>
                      <Label className='username-label' htmlFor='login-username'>
                        {intl.formatMessage({ id: 'Username' })}
                      </Label>
                      <Input
                        autoFocus
                        type='text'
                        value={value}
                        id='login-username'
                        className={classnames('username-input', { 'is-invalid-usrname': errors[USERNAME] })}
                        placeholder={intl.formatMessage({ id: 'Enter username' })}
                        onChange={onChange}
                      />
                    </div>
                  } />
                </Col>
              </Row>
              <Row className='align-items-end mt-5 mx-0'>
                <Col className='col-1 p-0 col-icon d-flex align-items-center justify-content-center'>
                  <Key className='key-icon' />
                </Col>
                <Col className='col-11 p-0'>
                  <Controller name={PASSWORD} control={control} render={({field: {value, onChange}}) => (
                    <div className={classnames('user-input d-flex flex-column h-100', { 'is-invalid': errors[PASSWORD] })}>
                      <Label className='pwd-label' for='login-pwd'>
                        {intl.formatMessage({ id: 'Password' })}
                      </Label>
                      <InputPasswordToggle
                        value={value}
                        id='login-password'
                        className={classnames({ 'is-invalid-pwd': errors[PASSWORD] })}
                        placeholder={intl.formatMessage({ id: 'Enter password' })}
                        onChange={onChange}
                      />
                    </div>
                  )} />
                </Col>
              </Row>

              <Row className='p-8-row mt-5 mx-0'>
                <Col className='offset-lg-1 col-11 d-flex flex-column flex-md-row p-0'>
                  <CustomInput type='checkbox' className='remember-me-chkbox ml-0' id='remember-me' label={intl.formatMessage({ id: 'RememberMe' })} />
                  <div className='forgot-pwd-link'>
                    <Link to='/forgot-password'><span className='forgot-pwd-text'>{intl.formatMessage({ id: 'ForgotPassword' })}</span></Link>
                  </div>
                </Col>
              </Row>

              <Row className='mx-0'>
                <Col className='offset-lg-1 col-11 d-flex flex-row p-0'>
                  <div className='login-btn mt-5' id='login-btn'>
                    <Button className='contained-login-btn' id='contained-login-btn'>
                      {intl.formatMessage({ id: 'Login' })}
                    </Button>
                  </div>
                </Col>
              </Row>

              <Row className='signup-row mx-0'>
                <Col className='offset-lg-1 col-11 d-flex flex-row p-0'>
                  <span>{intl.formatMessage({ id: 'NotAMember' })}</span>&nbsp;
                  <div className='signup-label'>{intl.formatMessage({ id: 'SignupNow' })}</div>
                </Col>
              </Row>
            </Form>
          </div>

          <Row className='d-lg-none btns-group'>
            <div onClick={(event) => openSocialPage(event, 'https://www.linkedin.com')}>
              <img className='li-icon-fluid' src={liIconSource} alt='Login V2' />
            </div>
            <div onClick={(event) => openSocialPage(event, 'https://www.facebook.com')}>
              <img className='fb-icon-fluid' src={fbIconSource} alt='Login V2' />
            </div>
            <div onClick={(event) => openSocialPage(event, 'https://www.google.com')}>
              <img className='gg-icon-fluid' src={ggIconSource} alt='Login V2' />
            </div>
          </Row>

          <div className='d-flex d-lg-none w-100 align-items-center img-wrapper'>
            <img className='img-fluid' src={source} alt='Login V2' />
          </div>
        </Col>
      </Row>
    </div>
  )
}

export default injectIntl(Login)
