import 'antd/dist/antd.css'
import { notification } from 'antd'
import Request from './request'
import { isObjEmpty } from '../utility/Utils'
export default class loginService {
  static openNotification(placement, message) {
    notification.error({
      message: `${message}`,
      placement,
    });
  };

  static async login(data={}) {
    return new Promise(resolve => {
      Request.send({
        method: "POST",
        path: "Staff/loginStaff",
        data
      }).then(result =>{
        const { data, statusCode } = result
        if(!isObjEmpty(data) && statusCode === 200) {
          resolve(data)
        } else if (!isObjEmpty(data) && statusCode === 400) {
          this.openNotification('topRight', data.message)
        } else if (isObjEmpty(data)) {
          this.openNotification('topRight', 'Internal Server Error!')
        } else {
          resolve({})
        }
      })
    }).catch(error => {
      console.error('Error during login:', error)
    })
  }
}